<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process182"></a>

<p><br></p>
<p><span class="rvts17">三座祭坛（</span><span class="rvts50">The Three Altars</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts41">任务坐标：</span>
<br><span class="rvts20">X:112,Y:267</span><span class="rvts21">，祭坛</span><span class="rvts20">1</span><span class="rvts21">（触发条件）</span>
<br><span class="rvts20">X:414,Y:301</span><span class="rvts21">，祭坛</span><span class="rvts20">2</span>
<br><span class="rvts20">X:484,Y:259</span><span class="rvts21">，祭坛</span><span class="rvts20">3</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">打败可怕的召唤怪，并与</span><span class="rvts20">3</span><span class="rvts21">处祭坛进行互动</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1199.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">在距离回廊树林传送点不远的地方，有一处阴暗的祭坛，这里有一只恐怖的召唤怪把守着。</span>
<br><span class="rvts21">这个怪物相当强大，会召唤出很多狼，并且这些狼自带提升召唤怪</span><span class="rvts20">BUFF</span><span class="rvts21">的技能。如果队伍等级较低，强烈建议之后再来。</span>
<br><span class="rvts21">集中活力攻击召唤怪会是最好结束战斗的办法。一旦时间拖下去，召唤怪身上的增益效果层数会越来越高，也就使得战斗变得越来越难。</span>
<br><span class="rvts21">打败召唤怪之后，必须使用队伍中的精灵角色与祭坛发起对话，不然的话，祭坛会一直说</span><span class="rvts20"></span><span class="rvts21">我在等一个人。。</span><span class="rvts20"></span><span class="rvts21">。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1200.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">第二处祭坛位置。（坐标</span><span class="rvts20">X:414,Y:301</span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1201.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">第三处祭坛位置。（坐标</span><span class="rvts20">X:484,Y:259</span><span class="rvts21">）该祭坛位置有一些虚空鹿把守，因此需要发生战斗。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



